import { Component } from '@angular/core';
import {DataService} from "../../providers/data-service/data-service";
import {IonicPage} from "ionic-angular";

import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';

@IonicPage()

@Component({
  selector: 'page-settings',
  templateUrl: 'settings.html',
})
export class SettingsPage {

  lang:string;
  counter:number = 0;
  count:number = 0;
  percentage:number = 0;


  constructor(
    private dataService:DataService,
    private transfer: FileTransfer, private file: File
  ) {
    this.lang = this.dataService.currLang;
  }

  ionViewDidLoad(){
    this.count = this.dataService.modulesData[0].module.series[0].chapters.filter(chapter=> chapter.media).length;
  }

  onChangeLang(){
    console.log(this.lang)
    this.dataService.setLanguage(this.lang);
  }



  async download(){

    this.counter = 0;
    this.percentage = 0;

    const chaptersMp3 = this.dataService.modulesData[0].module.series[0].chapters.filter(chapter=>chapter.media).map(chapter=>{
      return chapter.media.mp3
    });

    for(let i=0;i<chaptersMp3.length;i++){
      await fetch(chaptersMp3[i])
      this.counter++;
      this.percentage = Math.ceil((this.counter/this.count)*100)
    }

    console.log(chaptersMp3)

  }
}



